import React from 'react'
import styles from './ResumeEntry.module.scss'
import SpacedEmDash from '../utils/spacedEmDash'

//console.log('styles', styles)

export default function ResumeEntry(props) {
  const { entry } = props
  const singleDate = entry.dates.start === entry.dates.end
  return (
    <div className={styles.resumeEntry}>
      <div className={styles.resumeEntry__image}></div>
      <div className={styles.resumeEntry__content}>
        <div className={styles.resumeEntry__datesLocation}>
          <div className="range--dates">
            {singleDate ? (
              <span className="range__date--from">{entry.dates.start}</span>
            ) : (
              <span>
                <span className="range__date--from">{entry.dates.start}</span>
                <SpacedEmDash />
                <span className="range__date--to">{entry.dates.end}</span>
              </span>
            )}
          </div>
          <div className="resume-entry__location">
            <span className="resume-entry__city">{entry.location.city}</span>
            ,&nbsp;
            <span className="resume-entry__state">{entry.location.state}</span>
          </div>
        </div>
        <div className={styles.resumeEntry__heading}>
          <div className={styles.resumeEntry__position}>
            <span className={styles.resumeEntry__title}>{entry.title}</span>
            <SpacedEmDash />
            <span
              className="resume-entry__employer"
              dangerouslySetInnerHTML={{ __html: entry.employer }}
            />
          </div>
        </div>
        <div
          className={styles.resumeEntry__description}
          dangerouslySetInnerHTML={{ __html: entry.description }}
        />
      </div>
    </div>
  )
}
