import React from 'react';
import _uniq from 'array-uniq';
import ResumeEntry from './ResumeEntry';
import { sortByDates, sortByCategoryOrder } from '../utils/sorters';

export default function ResumeEntryCategoryContainer(props) {
  const {entries} = props;
  
  const entryCategories = _uniq(entries
    .sort(sortByCategoryOrder)
    .map(entry => entry.categoryName));
  const entriesByCategory = entryCategories
    .map(category => entries
      .filter(entry => entry.categoryName === category)
    )
  // console.log('entriesByCategory in RECC', entriesByCategory);
  return (
    <div>
      {
        entriesByCategory
        .map((category, idx) => {
            const catTitle = entryCategories[idx];
            return (
              <div key={catTitle}>
                <h3>{catTitle}</h3>
                  {
                    category
                    .sort(sortByDates)
                    .map(entry => {
                      return (
                        <ResumeEntry entry={entry} key={entry.employer} />
                      )
                    })
                  }
              </div>
            )
          }
        )
      }
    </div>
  )
}

