import React from 'react'
import { Helmet } from 'react-helmet'
import Bio from '../components/Bio'
import { rhythm, scale } from '../utils/typography'
import Resume from '../components/Resume'
import BlogIndex from '../components/BlogIndex'
import {
  getCategoryFromPath,
  getOrderFromPath,
  getPageSpecificClass,
} from '../utils/getDataFromPath'
import { graphql } from 'gatsby'

import Layout from '../components/layout'

const PageTemplate = ({data:pageData, location, pageContext:context}) => {
  //console.log('where is my data in the page template?', pageData)
  if (!pageData) {
    return <p>Loading ...</p>
  }
  const posts = pageData.postsMarkdown.edges
  const page = pageData.page
  const attributes = page.frontmatter.attributes
  const pagePath = pageData.sitePage.path
  const siteTitle = pageData.site.siteMetadata.title
  const isPost = context.type === 'post'
  const isResume = pagePath.indexOf('resume') > 0
  const isBlog = /(grumblings|blog|blag|musings)/.test(pagePath)
  const education = pageData.educationMarkdown.edges.map(school =>
    Object.assign({}, school.node.frontmatter.educationEntry, {
      description: school.node.html,
    })
  ) //TODO - define in md and get
  const skills = pageData.skillsData.edges.map(edge => edge.node)[0].skills
  const entries = pageData.entriesMarkdown.edges.map(entry => {
    const node = entry.node
    let categoryName = 'fake category'
    let categoryOrder = '00'
    if (node.fileAbsolutePath) {
      categoryName = getCategoryFromPath(node.fileAbsolutePath)
      categoryOrder = getOrderFromPath(node.fileAbsolutePath)
    }
    return Object.assign({}, node.frontmatter.resumeEntry, {
      description: node.html,
      categoryName,
      categoryOrder,
    })
  })
  // console.log('entries in layout', entries)
  const bio = context.bio
  const pageClass = getPageSpecificClass(location)

  return (
    <Layout location={location}>
      <div {...attributes} className={`template template_${pageClass}`}>
        <Helmet title={`${page.frontmatter.title} | ${siteTitle}`} />
        <h1>{page.frontmatter.title}</h1>

        {isPost && (
          <p
            style={{
              ...scale(-1 / 5),
              display: 'block',
              marginBottom: rhythm(1),
              marginTop: rhythm(-1),
            }}
          >
            {page.frontmatter.date}
          </p>
        )}

        <div dangerouslySetInnerHTML={{ __html: page.html }} />

        {isResume && (
          <Resume entries={entries} skills={skills} education={education} />
        )}

        {isBlog && <BlogIndex posts={posts} />}

        <hr
          style={{
            marginBottom: rhythm(1),
          }}
        />
        <Bio bio={bio} />
      </div>
    </Layout>
  )
}

export default PageTemplate

export const pageQuery = graphql`
  query PageBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    page: markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      html
      frontmatter {
        title
        pageClass
        date(formatString: "MMMM DD, YYYY")
        attributes {
          id
        }
      }
    }
    sitePage(path: { regex: $slug }) {
      path
    }
    educationMarkdown: allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "//education/" } }
    ) {
      edges {
        node {
          html
          fileAbsolutePath
          frontmatter {
            educationEntry {
              name
              dates {
                start
                end
              }
              location {
                city
                state
              }
            }
          }
        }
      }
    }
    entriesMarkdown: allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "//entries/" } }
    ) {
      edges {
        node {
          html
          fileAbsolutePath
          frontmatter {
            resumeEntry {
              title
              dates {
                start
                end
              }
              location {
                city
                state
              }
              employer
              category
            }
          }
        }
      }
    }
    skillsData: allSkillsYaml {
      edges {
        node {
          skills
        }
      }
    }
    skillsMarkdown: allMarkdownRemark(
      filter: {
        fileAbsolutePath: { regex: "//partials/" }
        frontmatter: { title: { eq: "ResumeSkills" } }
      }
    ) {
      edges {
        node {
          html
        }
      }
    }
    postsMarkdown: allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { fileAbsolutePath: { regex: "/.*/posts/.*/" } }
    ) {
      edges {
        node {
          excerpt
          html
          fields {
            slug
          }
          frontmatter {
            date(formatString: "DD MMMM, YYYY")
            title
          }
        }
      }
    }
  }
`
