import React from 'react'
import styles from './ResumeSkills.module.scss'
const md5 = require('blueimp-md5')

//takes in a raw array of skills parsed from JSON instead of html from markdown
const ResumeSkills = ({ skills }) => {
  //console.log('skills', skills)
  return (
    <ul className={styles.resumeSkills__list}>
      {skills.map(skill => (
        <li className={styles.resumeSkills__listItem} key={md5(skill)}>
          {skill}
        </li>
      ))}
    </ul>
  )
}

export default ResumeSkills
