import React from 'react'
import styles from './ResumeEntry.module.scss'
import { sortByDates } from '../utils/sorters'
import SpacedEmDash from '../utils/spacedEmDash'

export default function ResumeEducation(props) {
  const { education } = props

  const schoolsByDate = education.sort(sortByDates)
  // console.log('entriesByCategory in RECC', entriesByCategory);
  return (
    <div className="education">
      {schoolsByDate.map((school, idx) => {
        const singleDate = school.dates.start === school.dates.end
        return (
          <React.Fragment key={school.name}>
            <div className={styles.resumeEntry}>
              <div className={styles.resumeEntry__image}></div>
              <div className={styles.resumeEntry__content}>
                <div className={styles.resumeEntry__schoolName}>
                  {school.name}
                </div>
                <div className={styles.resumeEntry__datesLocation}>
                  <div className="range--dates">
                    {singleDate ? (
                      <span className="range__date--from">
                        {school.dates.start}
                      </span>
                    ) : (
                      <span>
                        <span className="range__date--from">
                          {school.dates.start}
                        </span>
                        <SpacedEmDash />
                        <span className="range__date--to">
                          {school.dates.end}
                        </span>
                      </span>
                    )}
                  </div>
                  <div className="resume-entry__location">
                    <span className="resume-entry__city">
                      {school.location.city}
                    </span>
                    ,&nbsp;
                    <span className="resume-entry__state">
                      {school.location.state}
                    </span>
                  </div>
                </div>
                <div
                  className={styles.resumeEntry__description}
                  dangerouslySetInnerHTML={{ __html: school.description }}
                />
              </div>
            </div>
          </React.Fragment>
        )
      })}
    </div>
  )
}
