export const sortByDates = (entryA, entryB) => {
  const aStart = entryA.dates.start
  const bStart = entryB.dates.start
  const aEnd = entryA.dates.end
  const bEnd = entryB.dates.end
  const aSingle = aStart === aEnd
  const bSingle = bStart === bEnd
  const isPresent = date => date.toString().toLowerCase() === 'present'
  // console.log('aStart', aStart)
  // console.log('bStart', bStart)
  // console.log('aEnd', aEnd)
  // console.log('bEnd', bEnd)
  // console.log('aSingle', aSingle)
  // console.log('bSingle', bSingle)
  // console.log('aIsPresent', isPresent(aEnd))
  if (aStart < bStart || (aSingle && aStart === bStart) || isPresent(bEnd)) {
    return 1
  }
  if (bStart < aStart || (bSingle && bStart === aStart) || isPresent(aEnd)) {
    return -1
  }
  return 0
}

export const sortByCategoryOrder = (entryA, entryB) => {
  const a = entryA.categoryOrder
  const b = entryB.categoryOrder
  if (a < b) {
    return -1
  }
  if (b < a) {
    return 1
  }
  return 0
}